/*  Author's custom styles */
.hide {
  display: none;
}

.show {
  display: block;
}

body {
  padding-top: 1em;
  font: 130%/160% Trebuchet MS, Georgia, serif;
  background: #000;
}

p {
  color: #017db1;
  letter-spacing: -0.05em;
  line-height: 1.3em;
}

strong {
  font-size: 140%;
}

a {
  color: #ffffff;
  text-decoration: none;
}

a:hover {
  color: #ceefff;
  text-decoration: none;
}

header#menu {
  text-align: center;
}

header#menu .currentlyLanguage {
  font-weight: bold;
  font-size: 24px;
}

footer {
  position: absolute;
  bottom: 0;
  text-align: center;
  width: 99%;
}

footer small {
  color: #656461;
  font-size: 65%;
}

/*  Media Queries */

@media only screen and (min-width: 480px) {
  /*  INTERMEDIATE: Menu */

  nav a {
    float: left;
    width: 27%;
    margin: 0 1.7%;
    padding: 25px 2%;
    margin-bottom: 0;
  }

  nav li:first-child a {
    margin-left: 0;
  }

  nav li:last-child a {
    margin-right: 0;
  }

  /*  INTERMEDIATE: IE Fixes */

  nav ul li {
    display: inline;
  }

  .oldie nav a {
    margin: 0 0.7%;
  }
}

@media only screen and (min-width: 768px) {
  /*  WIDE: CSS3 Effects */

  .header-container,
  .main aside {
    -webkit-box-shadow: 0 5px 10px #aaa;
    -moz-box-shadow: 0 5px 10px #aaa;
    box-shadow: 0 5px 10px #aaa;
  }

  /*  WIDE: Menu */

  .title {
    float: left;
  }

  nav {
    float: right;
    width: 38%;
  }

  /*  WIDE: Main */

  .main article {
    float: left;
  }
}

@media only screen and (min-width: 1140px) {
  /*  Maximal Width */

  .wrapper {
    width: 1026px; /* 1140px - 10% for margins */
    margin: 0 auto;
  }
}

/*  Helper classes */

.ir {
  background-color: transparent;
  border: 0;
  overflow: hidden;
  *text-indent: -9999px;
}

.ir:before {
  content: "";
  display: block;
  width: 0;
  height: 100%;
}

.hidden {
  display: none !important;
  visibility: hidden;
}

.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.visuallyhidden.focusable:active,
.visuallyhidden.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

.invisible {
  visibility: hidden;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  *zoom: 1;
}

/*  Print styles */

@media print {
  * {
    background: transparent !important;
    color: #000 !important; /* Black prints faster: h5bp.com/s */
    box-shadow: none !important;
    text-shadow: none !important;
  }

  a,
  a:visited {
    text-decoration: underline;
  }

  a[href]:after {
    content: " (" attr(href) ")";
  }

  abbr[title]:after {
    content: " (" attr(title) ")";
  }

  /*
     * Don't show links for images, or javascript/internal links
     */

  .ir a:after,
  a[href^="javascript:"]:after,
  a[href^="#"]:after {
    content: "";
  }

  pre,
  blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }

  thead {
    display: table-header-group; /* h5bp.com/t */
  }

  tr,
  img {
    page-break-inside: avoid;
  }

  img {
    max-width: 100% !important;
  }

  @page {
    margin: 0.5cm;
  }

  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }

  h2,
  h3 {
    page-break-after: avoid;
  }
}
